import expect from 'expect';
import request from 'supertest';
import app from '../app';

describe('POST /api/v1/menus', () => {
  it('should create a new menu', () => {
    const menu = {
      menuName: 'new menu2',
      date: '22/05/18',
      meals: ['rice', 'dodo', 'yam'],
    };
    request(app)
      .post('/api/v1/menus')
      .set('accept', 'application/json')
      .send({ menu })
      .expect(201)
      .expect((res) => {
        expect(res.body.menu).toEqual(menu);
      });
  });
  it('should not post menu if one of the key is missing', (done) => {
    const menu = {
      date: '22/05/18',
      meals: ['rice', 'dodo', 'yam'],
    };
    request(app)
      .post('/api/v1/menus')
      .send({ menu })
      .expect(400)
      .end(err => done(err));
  });
});

